:root {
    --bnm-border-color: #e0e0e0;
}

.bnm-block-title-wrap {
    position: relative;
    width: 100%; // make sure this isn't caught up in the flex styles.
    margin: var(--bnm-header-margin);
    padding: var(--bnm-header-padding);
}

.article-section-title {
    color: var(--bnm-header-color);
    font-size: var(--bnm-header-font-size);
    line-height: var(--bnm-header-line-height);
    letter-spacing: var(--bnm-header-letter-spacing);
    border-bottom: 2px solid #000000;
    margin: 0;
    padding: 0;
}

.bnm-bhs-2 {
    .article-section-title {
        border-bottom: none;
        border-left: 6px solid #000000;
        padding-left: 10px;
    }
}

.bnmfpovrlay,
.bnmslovrlay,
.bnmlnkovrlay {
	position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
}

.entry-meta {
    display: flex;
    flex-wrap: wrap;
}

.bnm-avatar {
    margin-right: 5px;
}

.bnm-avatar img {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    vertical-align: top;
}

.bnmfpovrlay.inside-gut-editor {
	cursor: pointer
}

img.bnm-fp-img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    max-width: none;
    max-height: none;
    object-fit: cover;
    outline: none;
    border: none;
    box-shadow: none;
    transition: transform .3s;
}

.bnm-category-list {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5em;
    font-size: 16px;
}

.bnm-category-list .post-categories {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 0.5em;
    padding: 0;
    gap: 0.125em 0.5em;
}

.bnm-box-cat .bnm-category-list .post-categories {
    gap: 0.5em;
}